
import Link from 'next/link';

interface IProps {
    clients: any,
    reverse: boolean
    sortBy: string
  }

const ClientsList = ({clients, reverse, sortBy} : IProps) => {
    const sort = reverse ? clients.reverse() : clients.reverse();
    const tableClients = sort.map((e:any) => (
      <tr key={e._id}>
        <td>{e.dateAdded}</td>
        <td>{e.name}</td>
        <td>{e.age}</td>
        <td>{e.address}</td>
        <td>
          <Link href="/clients/[id]" as={`/clients/${e._id}`}>
            <a>Go to client</a>
          </Link>
        </td>
      </tr>
    ));
    return tableClients;
  };

  export default ClientsList;